import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import React, { Component } from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import red from '@material-ui/core/colors/red';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Drawer from '@material-ui/core/Drawer';

import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Avatar from '@material-ui/core/Avatar';
import { Link } from "react-router-dom";
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';


const styles = {
    grow: {
        flexGrow: 1
    },
    menuButton: {
        marginLeft: -12,
        marginRight: 20,
    },
    avatarBig: {
        margin: 10,
        width: 60,
        height: 60,
    },
    avatar: {
        margin: 10
    },
}
const sideList = (
    <div style={{width: 250}}>
        <Avatar style={styles.avatarBig} color="primary">H</Avatar>
        <div style={{marginLeft: 10}}>
            <Typography variant="h5" color="inherit"  style={styles.grow}>Alex Peresta</Typography>
            <Typography variant="caption" fontWeight="fontWeightLight" color="default" paragraph={true} style={styles.grow}>alexperesta@gmail.com</Typography>

        </div>
        <Divider />
        <List>
            <ListItem button style={{background: red}} color="primary" component={Link} to="/dashboard">
                <ListItemText primary="Dashboard" />
            </ListItem>
            <ListItem button component={Link} to="/training">
                <ListItemText primary="Training" />
            </ListItem>
            <ListItem button style={{backgroundColor: red}} component={Link} to="/notepad">
                <ListItemText primary="Notepad" />
            </ListItem>
            <ListItem button  component={Link} to="/nutrition">
                <ListItemText primary="Nutrition"  />
            </ListItem>
            <ListItem button style={{backgroundColor: red}} component={Link} to="/hydration">
                <ListItemText primary="Hydration" />
            </ListItem>
            <ListItem button component={Link} to="/sleep">
                <ListItemText primary="Sleep" />
            </ListItem>
            <ListItem button style={{backgroundColor: red}} component={Link} to="/meditation">
                <ListItemText primary="Meditation" />
            </ListItem>
        </List>
    </div>
);

class Nav extends Component {
    constructor(props) {
        super(props);
        this.state = {
            left: false,
            anchorEl: null
        };
        this.toggleDrawer = this.toggleDrawer.bind(this);
        this.handleMenu = this.handleMenu.bind(this);
        this.handleClose = this.handleClose.bind(this);
    }
    componentDidMount() {

    }
    toggleDrawer = (side, open) => () => {
        this.setState({
            left: open,
        });
    };
    handleMenu = event => {
        this.setState({ anchorEl: event.currentTarget });
    };

    handleClose = () => {
        this.setState({ anchorEl: null });
    };

    render() {
        return (
            <section id="Navigation" styles={styles.root}>
                    <Drawer open={this.state.left} onClose={this.toggleDrawer('left', false)}>
                        <div
                            tabIndex={0}
                            role="button"
                            onClick={this.toggleDrawer('left', false)}
                            onKeyDown={this.toggleDrawer('left', false)}
                        >
                            {sideList}
                        </div>
                        <Typography variant="caption" fontWeight="fontWeightLight" color="default" style={{position: "absolute", bottom: 0, margin: 10}}> v. ALPHA 0.0.1</Typography>
                    </Drawer>
                    <AppBar position="static" >
                        <Toolbar>
                            <IconButton color="inherit" aria-label="Menu" style={styles.menuButton} onClick={this.toggleDrawer('left', true)}>
                                <MenuIcon />
                            </IconButton>
                            <Typography variant="h5" color="inherit" align="center" style={styles.grow}>
                                {this.props.title}
                            </Typography>
                            <div>
                                <Avatar style={styles.avatar} onClick={this.handleMenu}>H</Avatar>
                                <Menu
                                    id="simple-menu"
                                    anchorEl={this.state.anchorEl}
                                    open={Boolean(this.state.anchorEl)}
                                    onClose={this.handleClose}
                                >
                                    <MenuItem onClick={this.handleClose}>Profile</MenuItem>
                                    <MenuItem onClick={this.handleClose}>My account</MenuItem>
                                    <MenuItem onClick={this.handleClose}>Logout</MenuItem>
                                </Menu>
                            </div>
                        </Toolbar>
                    </AppBar>
            </section>
        );
    }
}



export default Nav;