import React, { Component } from 'react';
import './App.css';
import Nav from "./components/menu";
import Nutrition from "./components/nutrition";
import Hydration from "./components/water";
import Dashboard from "./components/dashboard";
import Training from "./components/training";
import Notepad from "./components/notepad";
import Sleep from "./components/sleep";
import Meditation from "./components/meditation";
import { BrowserRouter as Router, Route } from "react-router-dom";
import MuiThemeProvider from '@material-ui/core/styles/MuiThemeProvider';
import {createMuiTheme} from "@material-ui/core";

import red from '@material-ui/core/colors/red';
import orange from "@material-ui/core/colors/orange";
import yellow from "@material-ui/core/colors/yellow";
import green from "@material-ui/core/colors/green";
import blue from '@material-ui/core/colors/blue';
import indigo from "@material-ui/core/colors/indigo";
import deepPurple from "@material-ui/core/colors/deepPurple";


class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            components: [
                {
                    name: <Dashboard callBackFromParent={this.myCallback}/>,
                    path: "/dashboard",
                    icon: ""
                },
                {
                    name: <Training callBackFromParent={this.myCallback}/>,
                    path: "/training",
                    icon: ""
                },
                {
                    name: <Notepad callBackFromParent={this.myCallback}/>,
                    path: "/notepad",
                    icon: ""
                },
                {
                    name: <Nutrition callBackFromParent={this.myCallback}/>,
                    path: "/nutrition",
                    icon: ""
                },
                {
                    name: <Hydration callBackFromParent={this.myCallback}/>,
                    path: "/hydration",
                    icon: ""
                },
                {
                    name: <Sleep callBackFromParent={this.myCallback}/>,
                    path: "/sleep",
                    icon: ""
                },
                {
                    name: <Meditation callBackFromParent={this.myCallback}/>,
                    path: "/meditation",
                    icon: ""
                },
            ],
            theme: createMuiTheme({
                palette: {
                    primary: red
                }
            }),
            title: "default"
        };
    }
    myCallback = (dataFromChild) => {
        let newColor;
        let newTitle;
        if(dataFromChild === "green"){
            newColor = green;
            newTitle = "Nutrition";
        }else if(dataFromChild === "blue"){
            newColor = blue;
            newTitle = "Hydration";
        }else if(dataFromChild === "red"){
            newColor = red;
            newTitle = "Dashboard";
        }else if(dataFromChild === "orange"){
            newColor = orange;
            newTitle = "Training";
        }else if(dataFromChild === "yellow"){
            newColor = yellow;
            newTitle = "Notepad";
        }else if(dataFromChild === "indigo"){
            newColor = indigo;
            newTitle = "Sleep";
        }else if(dataFromChild === "deepPurple"){
            newColor = deepPurple;
            newTitle = "Meditation";
        }
        let newTheme = createMuiTheme({
            palette: {
                primary: newColor
            },
            typography: {
                useNextVariants: true,
            },
        });
        this.setState({
            theme: newTheme,
            title: newTitle
        })
    }
  render() {
    return (
          <Router>
              <MuiThemeProvider theme={this.state.theme}>
                  <div className="App">
                      <Nav title={this.state.title}></Nav>
                      {this.state.components.map((test) => (
                          <Route key={test.name} exact={true} path={test.path} render={() => (test.name)}></Route>
                      )) }
                  </div>
              </MuiThemeProvider>
          </Router>
    );
  }
}

export default App;
